import React, { useEffect, useState } from 'react';
import './style.scss';

const FeatureItem = () => {
  const [width, setWidth] = useState(window.innerWidth);

  const handleWindowSizeChange = () => {
    setWidth(window.innerWidth);
  }

  useEffect(() => {
    window.addEventListener('resize', handleWindowSizeChange);
    return () => {
      window.removeEventListener('resize', handleWindowSizeChange);
    }
  }, []);

  const isMobile = width <= 820;
  return (
    <>
      <article className="feature-item">
        <img
          src="./images/features-1.png"
          alt=""
          className="feature-item__image"
        />
        <div className="feature-item__text-container">
          <p className="feature-item__heading"> </p>
          <p className="feature-item__text">        .         .      .
          </p>

          <p className="feature-item__heading feature-item__heading_mt"> ,  </p>
          <p className="feature-item__text"> ,   .     .    .  ,   ,          .
          </p>
        </div>
      </article>

      <article className="feature-item">
        {!isMobile && (
          <img
            src="./images/features-2.png"
            alt=""
            className="feature-item__image"
          />
        )}
        <div className="feature-item__text-container">
          <p className="feature-item__heading">  </p>
          <p className="feature-item__text"> ,       .         .     : +7 776 256 1447</p>

          <p className="feature-item__heading feature-item__heading_mt">  </p>
          <p className="feature-item__text">       .           Kaspi.</p>
        </div>
      </article>
    </>
  );
};

export default FeatureItem;
